
header h3 {
  /* font-family: "Open Sans", sans-serif; */
  font-family: "Mali", sans-serif;
  font-weight: bold;
  font-size: 1.6em;
  margin: 0;
  
}

header nav {
  background-color: #343a40;
  width: 100%;
  
}

header nav a {
  display: inline-flex;
  color: #fff;
  /* text-decoration: none; */
  padding-top: 30px;
  padding-right: 20px;
  padding-bottom: 30px;
  padding-left: 30px;
  line-height: normal;
  transition: all 600ms ease;
}

header nav a:hover {
  background: #d7bde2;
  border-radius: 40px;
  text-decoration: none;
  color: #fff;
}

.menu {
  line-height: 0px;
  float: none;
  width: 100%;
  text-align: end;
}

div.noPadding {
    padding-left: 0;
    padding-right: 0;
}

div.noMargin {
    margin-left: 0;
    margin-right: 0;
}




.grilla-3 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; /* 30px 30px 30px or repeat(3, 1fr)*/
  grid-gap: 0px; 
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.grilla-4 {
  display: flex;
  flex-wrap: nowrap;
  padding-top: 0rem;
  padding-bottom: 0rem;
  justify-content: space-around;
}

.arreglo{
  width: 18%;
}

.center-foot{
  text-align: center;
  padding-top: 1rem;
}

.expand-center {
  margin: auto;
  width: 100%;
  text-align: center;
}

.detalles {
  font-family: "Mali", sans-serif;
  font-size: 1.5rem;
  color:white;
  text-align: center;
  background-color: #906096;
}




.titles {
    font-family: 'Mali', sans-serif ;
    font-size: 1.8rem;
    padding-bottom: 2rem;
    padding-top: 3rem;
    font-weight: bold;
  }

.fas { color: rgba(194, 146, 189, 0.705); } 

.color{
  color: rgb(108, 83, 165);
}
.center {
  margin: auto;
  width: 40%;
  text-align: center;
}
.contain {
  overflow: hidden;
  padding-bottom: 1rem;
  padding-top: 1rem;
  text-align: center;
}

footer {
  padding: 2.2rem;
}

.gradient {
  width: 100%;
  margin: 0;
  color: #fff;
  background: #654ea3; 
  background: linear-gradient(
    to right,
    #eaafc8,
    #654ea3
  ); 
}

section a, p, h3{
    color: #fff;
    font-family: "Open Sans", sans-serif;
    text-decoration: none;

  }

  section a:hover{
    color: slateblue;
    text-decoration: none;
  }

  section h3{
    display: block;
    font-size: 1.17em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    font-weight: bold;
    
  }

  footer p {
    margin: 0;
    color:  #2f3541;
  }

  
   
  
  
.fondo {
  background-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/Oveluna2.0/static/media/header_bg.f4f9391c.JPG);
  height: 85vh;
  background-size: cover;
}

.fuenteG {
  font-size: 4rem;
}

.header-content {
  position: absolute;
  top: 15%;
  left: 50%;
  margin-left: -185px;
  text-align: center;
  color: #fff;
  font-family: "Mali", sans-serif;
}



